import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";

const UserCard = ({ id, fullName, photoURL, firstName, lastName }) => {
  const urlUser = "https://vk.com/id" + id;
  return (
    <Div
      style={{
        backgroundColor: "#03023d",
      }}
    >
      <Cell
        style={{ width: "100%" }}
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopUsersDescription">
            <Div className="itemMainTopUsersLike">
              <Div style={{ whiteSpace: "nowrap", color: "white" }}>
                Возраст: 26
              </Div>
            </Div>
            <Div className="itemMainTopUsersLike">
              <Div style={{ whiteSpace: "nowrap", color: "white" }}>
                Город: Казань
              </Div>
            </Div>
          </Div>
        }
      >
        <Div
          style={{
            display: "flex",
            justifyContent: "start",
            alignItems: "center",
            flexDirection: "row",
            marginLeft: "10px",
            color: "white",
          }}
        >
          {firstName} {lastName}
        </Div>
      </Cell>
    </Div>
  );
};

export { UserCard };
