import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import {
  Input,
  Panel,
  Group,
  Div,
  Button,
  HorizontalScroll,
} from "@vkontakte/vkui";

import { ResultUserItem } from "../items";
import { GeneralInfoUser, UserCard } from "../components";

import { APIManager } from "../helpers";
const { callMethod } = APIManager;

const Home = ({
  id,
  go,
  fetchedUser,
  openPopoutSharing,
  openModal,
  USER_ID,
  onChangeHandlerId,
  getUserInfo,
  targetUserInfo,
  snackbar,
}) => {
  const [dataResult, setDataResult] = useState([
    {
      firstName: "Кирилл",
      lastName: "Андреев",
      idVk: "7777777",
      photoURL:
        "https://sun7-6.userapi.com/c857632/v857632332/1ee4a/MCaddnU_Gr0.jpg",
      amountLikes: 25,
    },
    {
      firstName: "Дмитрий",
      lastName: "Брюханов",
      idVk: "999999999",
      photoURL:
        "https://sun9-29.userapi.com/c857032/v857032227/7c38e/1ziePGYgGTk.jpg",
      amountLikes: 25,
    },
  ]);

  const [activeTab, setActiveTab] = useState("general");
  const [userInfo, setUserInfo] = useState({});

  useEffect(() => {
    callMethod("getUser").then(({ message }) => {
      setUserInfo(message);
    });
  }, []);

  const renderResult = dataResult.map((item, index) => {
    const urlUser = "https://vk.com/id" + item.idVk;
    return (
      <ResultUserItem
        key={index}
        urlUser={urlUser}
        photoURL={item.photoURL}
        firstName={item.firstName}
        lastName={item.lastName}
        amountLikes={item.amountLikes}
        activeTab={activeTab}
      />
    );
  });

  return (
    <Panel id={id}>
      <Div
        style={{
          // display: "flex",
          // justifyContent: "center",
          // alignItems: "center",
          // flexDirection: "column",
          padding: "70px 15px 15px 15px",
        }}
      >
        <Div className="panelHomeBalance">
          <Div className="flexSpaceBetweenRow">
            <UserCard
              fullName={userInfo.fullName}
              firstName={userInfo.firstName}
              lastName={userInfo.lastName}
              id={userInfo.id}
              photoURL={userInfo.photoURL}
            />
            <Div></Div>
          </Div>
          <Div className="flexSpaceBetweenRow">
            <Div>Попыток: </Div>
            <Div>{userInfo.attempts}</Div>
          </Div>
          <Div className="flexSpaceBetweenRow">
            <Div></Div>
            <Div>
              <Button
                style={{
                  backgroundColor: "green",
                  color: "white",
                }}
                mode="commerce"
                size="m"
                stretched
                onClick={openModal}
              >
                Пополнить
              </Button>
            </Div>
          </Div>
        </Div>

        <Div>Введите ссылку на пользователя</Div>
        <Div style={{ margin: "10px 0px 10px 0px" }}>
          <Input
            top="Введите ссылку на пользователя"
            placeholder={`https://vk.com/id${USER_ID}`}
            onChange={onChangeHandlerId}
            type="text"
          />
        </Div>

        {/* <FormLayout>
          <Input
            top="Введите ссылку на пользователя"
            placeholder={`https://vk.com/id${USER_ID}`}
            onChange={onChangeHandlerId}
            type="number"
          />
        </FormLayout> */}
        <Div>
          <Button
            style={{
              backgroundColor: "#03023d",
              color: "white",
              borderRadius: "13px",
            }}
            size="xl"
            level="2"
            onClick={getUserInfo}
            data-to="persik"
          >
            Узнать
          </Button>
        </Div>

        {targetUserInfo && (
          <>
            <HorizontalScroll>
              <Div
                style={{
                  display: "flex",
                  justifyContent: "space-around",
                  alignItems: "center",
                  flexDirection: "row",
                  margin: "10px 0px 10px 0px",
                }}
              >
                <Button
                  style={{
                    backgroundColor:
                      activeTab === "general" ? "#03023d" : "#2e2b2b",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  onClick={() => setActiveTab("general")}
                >
                  Общая
                </Button>
                <Button
                  style={{
                    backgroundColor:
                      activeTab === "likes" ? "#03023d" : "#2e2b2b",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  onClick={() => setActiveTab("likes")}
                >
                  Лайкает
                </Button>
                <Button
                  style={{
                    backgroundColor:
                      activeTab === "comments" ? "#03023d" : "#2e2b2b",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  onClick={() => setActiveTab("comments")}
                >
                  Комментирует
                </Button>
                <Button
                  style={{
                    backgroundColor:
                      activeTab === "groups" ? "#03023d" : "#2e2b2b",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  onClick={() => setActiveTab("groups")}
                >
                  Группы 18+
                </Button>
              </Div>
            </HorizontalScroll>

            {activeTab === "general" ? (
              <GeneralInfoUser targetUserInfo={targetUserInfo} />
            ) : (
              ""
            )}
            {activeTab === "likes" ? <Div>{renderResult}</Div> : ""}
            {activeTab === "comments" ? <Div>{renderResult}</Div> : ""}
            {activeTab === "groups" ? <Div>{renderResult}</Div> : ""}
          </>
        )}

        <Div>
          <Button
            style={{
              marginTop: "10px",
              backgroundColor: "green",
              color: "white",
              borderRadius: "13px",
            }}
            size="xl"
            level="2"
            onClick={openPopoutSharing}
            data-to="persik"
          >
            Поделиться
          </Button>
        </Div>
      </Div>
      {snackbar}
    </Panel>
  );
};

Home.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
  fetchedUser: PropTypes.shape({
    photo_200: PropTypes.string,
    first_name: PropTypes.string,
    last_name: PropTypes.string,
    city: PropTypes.shape({
      title: PropTypes.string,
    }),
  }),
};

export { Home };
