import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  ScreenSpinner,
  View,
  Snackbar,
  Avatar,
  ActionSheet,
  ActionSheetItem,
} from "@vkontakte/vkui";
import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";
import "@vkontakte/vkui/dist/vkui.css";

import { Home } from "./panels";
import { ModalBuy } from "./modal";

import "./styles/reset.sass";
import "./styles/icons.sass";
import "./styles/panels.sass";
import "./styles/items.sass";
import "./styles/components.sass";

import {
  APP_ID,
  GROUP_ID,
  QIWI_PUBLIC_KEY,
  APP_AVATAR,
  APP_IMG_SHARING,
  APP_IMG_SHARING_WALL,
  USER_ID,
} from "./config";

import { APIManager, sharing } from "./helpers";
const { callMethod } = APIManager;

const App = () => {
  let amount = 0;
  let targetId = "";
  let getPlatform = "";

  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);
  const [activeModal, setActiveModal] = useState(null);
  const [snackbar, setSnackbar] = useState(null);
  const [targetUserInfo, setTargetUserInfo] = useState(null);

  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "client_light";
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");
      setUser(user);
      setPopout(null);
    }
    fetchData();
  }, []);

  useEffect(() => {
    // Определение ОС
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        getPlatform = result.platform;

        console.log("result.platform", getPlatform);
      })
      .catch((err) => console.log(err));
  }, []);

  /**
   * Делает редирект на ссылку link
   */
  const fireEvent = (link) => {
    const a = document.createElement('a');
    a.href = link;
    a.target = '_blank';
    
    a.dispatchEvent(new window.MouseEvent('click', {
      view: window,
      bubbles: true,
      cancelable: true
    }));
  };

  function qiwiPay(type, choose) {
    window.QiwiCheckout.createInvoice({
      publicKey: QIWI_PUBLIC_KEY,
      amount: {
        Бронзовый: 9,
        Серебряный: 19,
        Золотой: 39,
        Платиновый: 69,
      }[type],
      comment: {
        Бронзовый: `bronze 0 ${USER_ID} (не менять)`,
        Серебряный: `silver 0 ${USER_ID} (не менять)`,
        Золотой: `gold ${choose} ${USER_ID} (не менять)`,
        Платиновый: `platinum ${choose} ${USER_ID} (не менять)`,
      }[type],
    })
      .then((response) => {
        console.log("qiwi vip", response);
      })
      .catch((error) => {
        console.log("QIWI VIP ERROR:", error);
      });
  }

  function payVkPay(type, choose) {
    bridge
      .sendPromise("VKWebAppOpenPayForm", {
        app_id: APP_ID,
        action: "pay-to-group",
        params: {
          amount: {
            Бронзовый: 9,
            Серебряный: 19,
            Золотой: 39,
            Платиновый: 69,
          }[type],
          group_id: GROUP_ID,
          description: {
            Бронзовый: `bronze 0 (не менять)`,
            Серебряный: `silver 0 (не менять)`,
            Золотой: `gold ${choose} (не менять)`,
            Платиновый: `platinum ${choose} (не менять)`,
          }[type],
        },
      })
      .then((response) => {
        console.log("vkpay vip", response);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  function onChangeHandlerAmount(e) {
    const value = e.target.value.trim();
    amount = +value;

    console.log("onChangeHandlerAmount targetId", amount);
  }

  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    targetId = value;

    console.log("onChangeHandlerId targetId", targetId);
  }

  function openPayment(type, choose) {
    setActiveModal(null);
    bridge
      .sendPromise("VKWebAppAllowMessagesFromGroup", {
        group_id: GROUP_ID,
      })
      .then((res) => {
        console.log("result", res);
        if (res) {
          setPopout(
            <>
              <ActionSheet onClose={() => setPopout(null)}>
                <ActionSheetItem
                  autoclose
                  onClick={() => qiwiPay(type, choose)}
                >
                  Оплатить через QIWI
                </ActionSheetItem>
                <ActionSheetItem
                  autoclose
                  onClick={() => payVkPay(type, choose)}
                >
                  Оплатить через VK Pay
                </ActionSheetItem>
              </ActionSheet>
            </>
          );
        }
      });
  }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  function openModal() {
    setActiveModal(
      <ModalBuy
        setActiveModal={setActiveModal}
        getDonateLink={getDonateLink}
        APP_AVATAR={APP_AVATAR}
        onChangeHandlerAmount={onChangeHandlerAmount}
      />
    );
  }

  function openPopoutSharing() {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={() => sharing("share-link")}>
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => sharing("copy-link", openAlert)}
          >
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => sharing("story")}>
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={(e) => sharing("share-wall", null, e)}
          >
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function getUserInfo() {
    if (targetId === "") {
      return openAlert("Введите ссылку на пользователя", "red");
    }
    console.log("getUserInfo targetId", targetId);
    callMethod("getUserInfo", {
      targetUser: targetId,
    }).then(({ status, message }) => {
      if (status === "success") {
        setTargetUserInfo(message);
      } else {
        openAlert(message, "red");
      }
    });
  }


  function getDonateLink(sum) {
    console.log(sum);
    callMethod("getDonateLink", {
      amount: sum,
    }).then(({ status, message }) => {
      console.log(message);
      if (status === "success") {
        fireEvent(message);
      } else {
        openAlert(message, "red");
      }
    });
  }

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <View
      activePanel={activePanel}
      popout={popout}
      header={false}
      modal={activeModal}
    >
      <Home
        id="home"
        fetchedUser={fetchedUser}
        go={go}
        openAlert={openAlert}
        snackbar={snackbar}
        openPopoutSharing={openPopoutSharing}
        openModal={openModal}
        USER_ID={USER_ID}
        onChangeHandlerId={onChangeHandlerId}
        getUserInfo={getUserInfo}
        targetUserInfo={targetUserInfo}
      />
    </View>
  );
};

export default App;
